body {
  background-image: url(https://cdnb.artstation.com/p/assets/images/images/004/498/987/large/meghan-wissler-mwissler-bob-sburgerrestaurant.jpg?1484104923);
  background-size: cover;
  background-repeat: no-repeat;
}

#header{
/* background-color: grey; */
margin-bottom: 50px;
padding: 10px;
padding-bottom: 30px;
text-align: center;
font-size: 60px;
font-family: 'Dosis', sans-serif;
}

.jumbotron {
  background-color: lightseagreen;
  opacity: 0.9;
  text-align: center;
  font-family: 'Dosis', sans-serif;
  font-size: 70px;
}


.App {
  text-align: center;
}

.App-title {
  font-size: 60px;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.bottom {
  /* background-color: azure */
}


